import Confirmation from './Confirmation.vue'

const prefix = "task_"

export default {
  name: 'Todos',
  data() {
    return {
      todos: [],
      addTodoForm: {
        description: '',
        is_completed: [],
      },
      confirmationMessage: '',
      showConfirmation: false,
      count_tasks: 0,
      completed_tasks: 0,
      title: "",
      newTodo: true,
      old_description: '',
    };
  },
  message: '',
  methods: {
    getTodos() {
      let uid = 1;
      for (const [key, value] of Object.entries(localStorage)) {
        if (key.startsWith(prefix)) {
          this.todos.push({'description': key.substr(5), "is_completed": parseInt(value), "uid": uid});
          if (value == "1") {
            this.completed_tasks++;
          }
          uid++;
        }
      }
      this.count_tasks = this.todos.length;
    },
    resetForm() {
      this.addTodoForm.description = "";
      this.addTodoForm.is_completed = [];
      this.title = "Создать задачу";
      this.newTodo = true;
    },
    updateState() {
      this.todos = [];
      this.completed_tasks = 0;
      this.getTodos();
    },
    onSubmit(event) {
      event.preventDefault();
      this.$refs.addTodoModal.hide();
      let is_completed = this.addTodoForm.is_completed.length > 0 ? "1" : "0";
      let task = prefix + this.addTodoForm.description;
      if (this.newTodo) {
        localStorage.setItem(task, is_completed);
        this.confirmationMessage = `Задача "${this.addTodoForm.description}" добавлена!`;
      } else {
        if (this.old_description != this.addTodoForm.description) {
          localStorage.removeItem(prefix + this.old_description);
        }
        localStorage.setItem(task, is_completed)
        this.confirmationMessage = `Задача обновлена!`;
      }
      this.old_description = '';
      this.showConfirmation = true;
      this.resetForm();
      this.updateState();
    },
    onReset(event) {
      event.preventDefault();
      this.$refs.addTodoModal.hide();
      this.resetForm();
    },
    updateTodo(todo) {
      this.old_description = todo.description;
      this.addTodoForm.description = todo.description;
      this.addTodoForm.uid = todo.uid;
      this.addTodoForm.is_completed = todo.is_completed == "1" ? [true] : [];
      this.title = "Изменить задачу"
      this.newTodo = false;
    },
    deleteTodo(todo) {
      localStorage.removeItem(prefix + todo.description);
      let description = todo.description;
      this.updateState();
      this.confirmationMessage = `Задача "${description}" удалена!`;
      this.showConfirmation = true;
    },
    resetAlert() {
      this.showConfirmation = false;
    },
  },
  components: {
    confirmation: Confirmation,
  },
  created() {
    this.getTodos();
  },
};
